/* eslint-disable no-unused-vars */
/* eslint-disable no-unused-vars */
<template>
  <div>
    <!--  -->
    <Menu @download="download" @upload="upload" @command="command" ref="menu" />
    <el-row :gutter="10" justify="center" type="flex" class="margin-10">
      <el-col :span="4">
        <!--  -->
        <draggable
          id="components"
          tag="div"
          :list="elements"
          :group="{ name: 'addflow', pull: 'clone', put: false }"
          :move="moveNode"
          @end="addNewNode"
          drag-class="ghost"
        >
          <!--  -->
          <div
            class="component-item no-user-select"
            v-for="item in elements"
            :key="item.id"
          >
            {{ item.text }}
          </div>
        </draggable>
      </el-col>

      <el-col :span="20">
        <!--  -->
        <draggable
          id="canvas-wrapper"
          tag="div"
          :list="list"
          group="addflow"
          draggable=".drag"
        >
          <canvas ref="canvas" height="800" width="800"></canvas>
        </draggable>
      </el-col>
    </el-row>

    <!-- Dialog -->
    <drawer-box :flow="flow" ref="drawer" />
  </div>
</template>

<script>
import draggable from "vuedraggable";
import Menu from "./Menu.vue";
import { Lassalle } from "../js/addflow.js";
import elements from "../js/mockData";
import JSONFlow from "../js/jsonflow.js";
import DrawerBox from "./DrawerBox.vue";
export default {
  name: "AddFlow",
  components: {
    draggable,
    Menu,
    DrawerBox,
  },

  data() {
    return {
      // flow
      flow: null,
      // 
      dialog: false,
      // Node
      selectedNode: {
        text: "",
      },
      // flow
      config: {
        canDrawNode: false,
        gridSnap: false,
        gridDraw: false,
        fillStyle: "black",
      },
      // 
      list: [],
      // 
      elements: elements,
      // 
      nodes: [],
    };
  },

  methods: {
    //flow
    addNewNode(event) {
      let temp = this.list.pop();
      temp.x = Math.round(
        event.originalEvent.layerX / this.flow.zoom - temp.w / 2
      );
      temp.y = Math.round(
        event.originalEvent.layerY / this.flow.zoom - temp.h / 2
      );
      this.flow.beginUpdate();
      let node = this.flow.addNode(temp.x, temp.y, temp.w, temp.h, temp.text);
      if (temp.polygon) {
        node.polygon = temp.polygon;
      }
      node.shapeFamily = temp.shapeFamily;
      this.flow.endUpdate();
      this.updateNodes();
    },

    // 
    moveNode(source, event) {
      let item = source.draggedContext.element;
      let x = Math.round(event.layerX / this.flow.zoom - item.w /2);
      let y = Math.round(event.layerY / this.flow.zoom - item.h /2);
      for(let node of this.nodes){
        if(node.x == x){
          console.log(x);
          return;
        }
        if(node.y == y){
          console.log(y);
          return;
        }
      }
    },

    // nodes
    updateNodes() {
      console.log("node");
      let items = this.flow.getItems();
      this.nodes = [];
      for (let item of items) {
        if (this.flow.isNode(item)) {
          this.nodes.push(item);
        }
      }
    },

    //node
    showInfo() {
      this.$refs.drawer.showInfo();
    },
    // flowjson
    download() {
      let json = JSONFlow.toJSON(this.flow);
      var a = document.createElement("a");
      var file = new Blob([json], { type: "text/plain" });
      a.href = URL.createObjectURL(file);
      a.download = "addflow.json";
      a.click();
    },
    // jsonflow
    upload(json) {
      this.flow.clear();
      JSONFlow.fromJSON(this.flow, json);
    },
    // 
    command(type) {
      if (type === "undo") {
        this.flow.taskManager.undo();
      }
      if (type === "redo") {
        this.flow.taskManager.redo();
      }
      if (type === "refresh") {
        this.flow.refresh();
      }
      if (type === "delete") {
        this.flow.deleteSel();
      }
      if (type === "zoom-in") {
        this.flow.unselectAll();
        this.flow.zoom += 0.1;
        this.flow.refresh();
      }
      if (type === "zoom-out") {
        this.flow.unselectAll();
        this.flow.zoom -= 0.1;
        this.flow.refresh();
      }
    },
    // 
    // scroll(e){
    //   let direct = e.wheelDeltaY;
    //   if(direct > 0){
    //     this.$refs.menu.zoomIn();
    //   }else{
    //     this.$refs.menu.zoomOut();
    //   }
    // }
  },

  created() {
    // Ctrl+z
    this.$shortcut.bind("ctrl+z", (_) => {
      this.command("undo");
    });
    // Ctrl+r
    this.$shortcut.bind("ctrl+r", (_) => {
      this.command("redo");
    });
    // Delete
    this.$shortcut.bind("delete", (_) => {
      this.command("delete");
    });
    // node
    document.addEventListener("dblclick", this.showInfo, false);
  },

  mounted() {
    const canvas = this.$refs.canvas;
    this.flow = new Lassalle.Flow(canvas);

    // 
    this.flow.canDrawNode = false; //Node
    this.flow.gridSnap = true; // 
    this.flow.gridDraw = false; // 
    this.flow.gridSizeX = 10; // 
    this.flow.gridSizeY = 10; // 
    this.flow.fillStyle = "yellow"; //
    this.flow.mouseSelection = "selection"; // 
    //
    this.flow.nodeModel.isContextHandle = false; // 
    this.flow.nodeModel.strokeStyle = "black";
    this.flow.nodeModel.textFillStyle = "black";
    this.flow.nodeModel.lineWidth = 2;
    this.flow.nodeModel.textLineHeight = 15;
    this.flow.pinSize = 10; //
    this.flow.nodeModel.pins = [
      [0, 25],
      [0, 50],
      [0, 75],
      [25, 0],
      [50, 0],
      [75, 0],
      [100, 25],
      [100, 50],
      [100, 75],
      [25, 100],
      [50, 100],
      [75, 100],
    ];

    //
    this.flow.linkModel.strokeStyle = "black";
    this.flow.linkModel.textFillStyle = "black";
    this.flow.linkModel.lineStyle = "orthogonal";
    this.flow.linkModel.lineWidth = 2;
    this.flow.linkModel.isShadowed = true;

    this.flow.refresh();
  },
};
</script>

<style scoped>
#components {
  height: 90vh;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  overflow: auto;
  justify-content: center;
  border-style: solid;
  background: greenyellow;
}
.component-item {
  width: 80px;
  height: 80px;
  border-color: black;
  border-style: solid;
  text-align: center;
  line-height: 80px;
  margin: 10px;
}
.ghost {
  background-color: red;
}
#canvas-wrapper {
  border-style: solid;
  width: 100%;
  height: 90vh;
  overflow: auto;
  background-color: yellow;
}
.margin-10 {
  margin-top: 10px;
}
</style>